import Vue from 'vue'
import { translate } from '@nextcloud/l10n'
import EventBus from './EventBus'
import OpenOTPSignModal from './OpenOTPSignModal'
import OpenOTPSealModal from './OpenOTPSealModal'
import axios from '@nextcloud/axios'
import { generateUrl } from '@nextcloud/router'

Vue.prototype.$t = translate

// For Sign Modal
const signModalHolderId = 'ootpsignModalHolder'
const signModalHolder = document.createElement('div')
// For Seal Modal
const sealModalHolderId = 'ootpsealModalHolder'
const sealModalHolder = document.createElement('div')

const baseUrl = generateUrl('/apps/openotp_sign')
let enabledOtp = {}
let source = null

const CancelToken = axios.CancelToken
source = CancelToken.source()

// Get data from NxC settings
axios.get(baseUrl + '/check_enabled_otp', {
}, {
	cancelToken: source.token,
})
	.then(response => {
		enabledOtp = response.data

		// Sign enabled
		if (enabledOtp.enable_otp_sign) {
			// Create Sign Tag
			signModalHolder.id = signModalHolderId
			document.body.append(signModalHolder)

			// eslint-disable-next-line
			new Vue({
				el: signModalHolder,
				render: h => {
					return h(OpenOTPSignModal)
				},
			})

			OCA.Files.fileActions.registerAction({
				mime: 'file',
				name: 'OpenOTPSign',
				permissions: OC.PERMISSION_READ,
				iconClass: 'custom-icon-signature',
				actionHandler: (filename, context) => {
					EventBus.$emit('ootp-sign-click', { filename })
				},
				displayName: t('openotp_sign', 'Sign with OpenOTP'),
				order: -100,
			})
		}

		// Seal enabled
		if (enabledOtp.enable_otp_seal) {
			// Create Seal Tag
			sealModalHolder.id = sealModalHolderId
			document.body.append(sealModalHolder)

			// eslint-disable-next-line
			new Vue({
				el: sealModalHolder,
				render: h => {
					return h(OpenOTPSealModal)
				},
			})

			OCA.Files.fileActions.registerAction({
				mime: 'file',
				name: 'OpenOTPSeal',
				permissions: OC.PERMISSION_READ,
				iconClass: 'custom-icon-signature',
				actionHandler: (filename, context) => {
					EventBus.$emit('ootp-seal-click', { filename })
				},
				displayName: t('openotp_sign', 'Seal with OpenOTP'),
				order: -99,
			})
		}
	})
	.catch(error => {
		// eslint-disable-next-line
		console.log(error)
	})
