	* along with this program.  If not, see <https://www.gnu.org/licenses/>.
	*
	-->
	<div>
		<Modal v-if="modal" size="large" @close="closeModal">
			<img
				id="rcdevsLogo"
				:src="rcdevsLogo">
			<div class="modal__content">
				<h1>{{ $t('openotp_sign', 'OpenOTP Sign') }}</h1>
				<img v-if="checkingSettings" :src="loadingImg">
				<p v-else-if="!enabledOtp.enable_otp_sign || (!signTypes.sign_type_standard && !signTypes.sign_type_advanced) || !settingsOk"
					id="error_settings"
					class="alert alert-danger">
					<span>
						{{ warningServer }}
					</span>
				</p>
				<div v-else>
					<img v-if="!success" :src="mobileSigningImg" style="max-height: 200px;">
					<p v-else id="green-tick">
						&#10003;
					</p>
					<p>
						{{ signMessage }} <strong>{{ filename }}</strong>
					</p>
					<p v-if="error" class="error">
						{{ errorMessage }}
					</p>
					<br>
					<div v-if="!requesting && !success">
						<CheckboxRadioSwitch
							:checked.sync="recipientType"
							value="self"
							name="recipient_radio"
							type="radio">
							{{ $t('openotp_sign', 'Self-signature') }}
						</CheckboxRadioSwitch>
						<div class="flex-container">
							<CheckboxRadioSwitch
								:checked.sync="recipientType"
								value="nextcloud"
								name="recipient_radio"
								type="radio">
								{{ $t('openotp_sign', 'Signature by a Nextcloud user:') }}
							</CheckboxRadioSwitch>
							<Multiselect
								v-model="localUser"
								:options="formattedOptions"
								label="displayName"
								track-by="uid"
								:user-select="true"
								style="width: 400px"
								@select="checkNextcloudRadio"
								@search-change="localUserSearchChanged">
								<template #singleLabel="{ option }">
									<ListItemIcon
										v-bind="option"
										:title="option.displayName"
										:avatar-size="24"
										:no-margin="true"
										style="width: 380px;" />
								</template>
							</Multiselect>
						</div>
						<button v-if="signTypes.sign_type_standard" type="button" @click="standardSignature">
							{{ signTypes.sign_type_standard_label }}
						</button>
						<button v-if="signTypes.sign_type_advanced" type="button" @click="advancedSignature">
							{{ signTypes.sign_type_advanced_label }}
						</button>
					</div>
					<div v-if="requesting">
						<img :src="loadingImg">
					</div>
					<div v-if="success">
						<button type="button" class="primary" @click="closeModal">
							{{ $t('openotp_sign', 'Close') }}
						</button>
					</div>
				</div>
			</div>
		</Modal>
	</div>
</template>
<script>
import Modal from '@nextcloud/vue/dist/Components/Modal'
import CheckboxRadioSwitch from '@nextcloud/vue/dist/Components/CheckboxRadioSwitch'
import Multiselect from '@nextcloud/vue/dist/Components/Multiselect'
import ListItemIcon from '@nextcloud/vue/dist/Components/ListItemIcon'
import EventBus from './EventBus'
import queryString from 'query-string'
import axios from '@nextcloud/axios'
import { generateUrl, generateFilePath } from '@nextcloud/router'

export default {
	name: 'OpenOTPSignModal',
	components: {
		Modal,
		CheckboxRadioSwitch,
		Multiselect,
		ListItemIcon,
	},
	data() {
		return {
			modal: false,
			checkingSettings: true,
			checkingSignTypes: true,
			requesting: false,
			success: false,
			error: false,
			errorMessage: '',
			source: null,
			enabledOtp: [],
			settingsOk: false,
			signTypes: [],
			signLabelStandard: '',
			signLabelAdvanced: '',
			recipientType: 'self',
			localUser: '',
			externalUserEmail: '',
			formattedOptions: [],
		}
	},
	mounted() {
		this.rcdevsLogo = generateFilePath('openotp_sign', '', 'img/') + 'rcdevs-96.png'
		this.mobileSigningImg = generateFilePath('openotp_sign', '', 'img/') + 'mobile-signing.png'
		this.loadingImg = generateFilePath('core', '', 'img/') + 'loading.gif'

		EventBus.$on('ootp-sign-click', payload => {
			this.showModal()
			this.filename = payload.filename
			this.signMessage = t('openotp_sign', 'Digital signature of file')
		})
	},
	methods: {
		showModal() {
			const baseUrl = generateUrl('/apps/openotp_sign')
			this.warningServer = ''

			const CancelToken = axios.CancelToken
			this.source = CancelToken.source()
			axios.get(baseUrl + '/check_enabled_otp', {
			}, {
				cancelToken: this.source.token,
			})
				.then(response => {
					this.enabledOtp = response.data

					// Sign enabled
					if (this.enabledOtp.enable_otp_sign) {
						this.requesting = false
						this.success = false
						this.error = false
						this.errorMessage = ''

						const CancelToken = axios.CancelToken
						this.source = CancelToken.source()
						axios.get(baseUrl + '/check_settings', {
						}, {
							cancelToken: this.source.token,
						})
							.then(response => {
								this.checkingSettings = false
								this.settingsOk = response.data
								if (!this.settingsOk) {
									this.warningServer = t('openotp_sign', 'You have to enter the OpenOTP server URL in the OpenOTP Sign settings prior to sign any document')
								}
							})
							.catch(error => {
								// eslint-disable-next-line
								console.log(error)
							})

						this.source = CancelToken.source()
						axios.get(baseUrl + '/check_sign_types', {
						}, {
							cancelToken: this.source.token,
						})
							.then(response => {
								this.checkingSignTypes = false
								this.signTypes = response.data

								switch (true) {
								case !this.signTypes.sign_type_standard && !this.signTypes.sign_type_advanced:
									this.warningServer = t('openotp_sign', 'Cannot sign when all signature types are disabled')
									break

								case !this.signTypes.sign_type_standard || !this.signTypes.sign_type_advanced:
									this.signTypes.sign_type_standard_label = t('openotp_sign', 'Signature')
									this.signTypes.sign_type_advanced_label = t('openotp_sign', 'Signature')
									break

								default:
									this.signTypes.sign_type_standard_label = t('openotp_sign', 'Standard signature')
									this.signTypes.sign_type_advanced_label = t('openotp_sign', 'Advanced signature')
									break
								}
							})
							.catch(error => {
								// eslint-disable-next-line
								console.log(error)
							})
					} else {
						// Sign disabled
						this.warningServer = t('openotp_sign', 'Sign process is disabled')
					}
					this.modal = true
				})
		},
		checkNextcloudRadio() {
			this.recipientType = 'nextcloud'
		},
		localUserSearchChanged(searchQuery, id) {
			if (searchQuery.length >= 3) {
				const baseUrl = generateUrl('/apps/openotp_sign')
				const CancelToken = axios.CancelToken
				this.source = CancelToken.source()
				axios.get(baseUrl + '/get_local_users?searchQuery=' + searchQuery, {
				}, {
					cancelToken: this.source.token,
				})
					.then(response => {
						this.formattedOptions = response.data.map(item => {
							return {
								uid: item.uid,
								displayName: item.display_name,
								subtitle: item.email,
								icon: 'icon-user',
								isNoUser: false,
							}
						})
					})
					.catch(error => {
						// eslint-disable-next-line
						console.log(error)
					})
			} else {
				this.formattedOptions = []
			}
		},
		checkExternalRadio() {
			if (this.externalUserEmail.length > 0) {
				this.recipientType = 'external'
			}
		},
		closeModal() {
			if (this.source !== null) {
				this.source.cancel('Operation canceled by the user.')
				this.source = null
			}

			if (this.success) {
				FileList.reload()
			}
			this.modal = false
			this.warningServer = ''
		},
		standardSignature() {
			if (!this.filename.toLowerCase().endsWith('.pdf')) {
				alert(t('openotp_sign', 'Standard signature is possible only with PDF files'))
				return
			}

			if (this.recipientType === 'self') {
				this.syncStandardSignature()
			} else if (this.recipientType === 'nextcloud') {
				this.asyncLocalStandardSignature()
			}
		},
		syncStandardSignature() {
			this.error = false
			this.requesting = true
			const baseUrl = generateUrl('/apps/openotp_sign')

			const CancelToken = axios.CancelToken
			this.source = CancelToken.source()
			axios.post(baseUrl + '/standard_sign', {
				path: this.getFilePath(),
			}, {
				cancelToken: this.source.token,
			})
				.then(response => {
					this.requesting = false
					if (response.data.code === '1') {
						this.success = true
					} else {
						this.error = true
						this.errorMessage = t('openotp_sign', 'Error: ') + t('openotp_sign', response.data.message)
					}
				})
				.catch(error => {
					this.requesting = false
					this.error = true
					this.errorMessage = error
				})
		},
		asyncLocalStandardSignature() {
			if (this.recipientType === 'nextcloud' && !this.localUser) {
				return
			}

			this.error = false
			this.requesting = true
			const baseUrl = generateUrl('/apps/openotp_sign')

			const CancelToken = axios.CancelToken
			this.source = CancelToken.source()
			axios.post(baseUrl + '/async_local_standard_sign', {
				path: this.getFilePath(),
				username: this.localUser.uid,
				email: this.localUser.subtitle,
			}, {
				cancelToken: this.source.token,
			})
				.then(response => {
					this.requesting = false
					if (response.data.code === '2') {
						this.success = true
					} else {
						this.error = true
						this.errorMessage = t('openotp_sign', 'Error: ') + t('openotp_sign', response.data.message)
					}
				})
				.catch(error => {
					this.requesting = false
					this.error = true
					this.errorMessage = error
				})
		},
		advancedSignature() {
			if (this.recipientType === 'self') {
				this.syncAdvancedSignature()
			} else if (this.recipientType === 'nextcloud') {
				this.asyncLocalAdvancedSignature()
			}
		},
		syncAdvancedSignature() {
			this.error = false
			this.requesting = true
			const baseUrl = generateUrl('/apps/openotp_sign')

			const CancelToken = axios.CancelToken
			this.source = CancelToken.source()
			axios.post(baseUrl + '/advanced_sign', {
				path: this.getFilePath(),
			}, {
				cancelToken: this.source.token,
			})
				.then(response => {
					this.requesting = false
					if (response.data.code === '1') {
						this.success = true
					} else {
						this.error = true
						this.errorMessage = t('openotp_sign', 'Error: ') + t('openotp_sign', response.data.message)
					}
				})
				.catch(error => {
					this.requesting = false
					this.error = true
					this.errorMessage = error
				})
		},
		asyncLocalAdvancedSignature() {
			if (this.recipientType === 'nextcloud' && !this.localUser) {
				return
			}

			this.error = false
			this.requesting = true
			const baseUrl = generateUrl('/apps/openotp_sign')

			const CancelToken = axios.CancelToken
			this.source = CancelToken.source()
			axios.post(baseUrl + '/async_local_advanced_sign', {
				path: this.getFilePath(),
				username: this.localUser.uid,
				email: this.localUser.subtitle,
			}, {
				cancelToken: this.source.token,
			})
				.then(response => {
					this.requesting = false
					if (response.data.code === '2') {
						this.success = true
					} else {
						this.error = true
						this.errorMessage = t('openotp_sign', 'Error: ') + t('openotp_sign', response.data.message)
					}
				})
				.catch(error => {
					this.requesting = false
					this.error = true
					this.errorMessage = error
				})
		},
		getFilePath() {
			const parsed = queryString.parse(window.location.search)
			if (parsed.dir === '/') {
				return this.filename
			} else {
				return parsed.dir + '/' + this.filename
			}
		},
	},
}
</script>
<style scoped>
	.modal__content {
		margin: 50px;
		text-align: center;
	}

	h1 {
		font-size: 2em;
		font-weight: bold;
		margin-bottom: 10px;
	}

	#green-tick {
		font-size: 150px;
		color: green;
		margin-top: 100px;
		margin-bottom: 100px;
	}

	.error {
		color: red;
	}

	.alert-danger {
		color: #721c24;
		background-color: #f8d7da;
		border-color: #f5c6cb;
	}

	.alert {
		display: block;
		position: relative;
		padding: .75rem 1.25rem;
		margin-bottom: 1rem;
		border: 1px solid transparent;
		border-radius: .25rem;
	}

	#error_settings {
		margin-top: 25px;
	}

	.flex-container {
		display: flex;
		align-items: center;
		gap: 8px;
	}

	.flex-container:last-of-type {
		margin-bottom: 32px;
	}

	input {
		flex: 1 1 auto;
	}

	#rcdevsLogo {
		padding-top: 6px;
		padding-left: 6px;
	}
</style>
